@import url("https://fonts.googleapis.com/css2?family=Audiowide&family=Libre+Barcode+39&family=Rubik+Maze&display=swap");

.App {
  display: flex;
  flex-direction: column;
}

.header {
  background-color: #282c34;
  min-height: 20vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 10px;
}

h1 {
  font-family: "Audiowide", cursive;
  font-size: 50px;
  margin-right: 300px;
  margin-left: 450px;
  display: flex;
  flex-direction: row;
}

img {
  display: flex;
  align-content: flex-end;
  justify-content: flex-end;
  height: 5em;
  width: 5em;
  margin-right: 10px;
  margin-left: 30px;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.formContainer {
  font-family: "Audiowide", cursive;
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  align-content: flex-end;
  justify-self: flex-end;
  margin-left: 10px;
  width: 200px;
}

input[type="text"] {
  text-align: center;
  /* font-size: 18px; */
}

.form {
  border: solid grey 1px;
  border-radius: 5px;
  margin-left: 10px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  height: 30em;
  min-height: 100%;
  width: 1120px;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  gap: 3px;
  padding: 1rem;
  align-content: center;
}

th,
td {
  /* font-family: 'Audiowide',cursive; */
  border: 1px solid #ffffff;
  text-align: center;
  align-content: center;
  padding: 8px;
  font-size: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

th {
  display: flex;
  justify-content: center;
  background-color: rgb(117, 201, 250);
  border-radius: 5px;
  min-width: 162px;
}

td {
  display: flex;
  justify-content: center;
  background-color: rgb(205, 235, 253);
  border-radius: 5px;
  width: 160px;
}

.editInput {
  width: 160px;
}

form td:last-child {
  display: flex;
  justify-content: space-evenly;
}

form * {
  font-size: 18px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 5px;
}

#submit {
  align-self: center;
  align-items: center;
  width: 100px;
  cursor: pointer;
  border-radius: 5px;
}

.actions {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

thead {
  display: flex;
  flex-direction: row;
  min-width: 900px;
}

tbody {
  display: flex;
  flex-direction: column;
  min-width: 900px;
}

tr {
  display: flex;
  flex-direction: row;
  min-width: 900px;
}

.button {
  min-width: 80px;
}

#edit {
  background-color: rgb(33, 143, 99);
  color: white;
  align-items: center;
  border-radius: 5px;
  cursor: pointer;
}

button[color="red"] {
  background-color: rgb(230, 73, 73);
  color: white;
  align-items: center;
  border-radius: 5px;
  cursor: pointer;
}

input[name="edit"] {
  width: 95%;
}

.saveBtn {
  background-color: rgb(56, 168, 233);
  color: white;
  align-items: center;
  border-radius: 5px;
  cursor: pointer;
  min-width: 80px;
}

.cancelBtn {
  background-color: orange;
  color: white;
  align-items: center;
  border-radius: 5px;
  cursor: pointer;
  min-width: 80px;
}
